import React from 'react';

class App extends React.Component {

    constructor(props) {
        super (props);

        this.state = {
            count: 0,
            stack: []
        };
        this.handleClick = this.handleClick.bind(this);
    }

    render () {
        return (
            <div>
                <button onClick={this.handleClick}>Click {`${this.state.count}`}</button>
                {this.state.stack.map(n => <h3>n: {n}</h3>)}
            </div>
        );
    }

    handleClick = (e) => {
        let {count, stack} = this.state;

        stack.push(count++);

        this.setState ({
            count,
            stack
        });
    }
}

export default App;